<script>
import { truncate } from 'fs';
export default {
  props: {
    isShow: {
      type: Boolean,
      value: false
    }
  },
  data() {
    return {
      placeholder: '',
      schoolParams: {
        nick: '',
        address: '',
        mobile: '',
        email: '',
        pwd: '',
        department: '',
        fromadd: '',
        ext: '',
        teamtype: '1'
      },
      cpParams: {
        nick: '',
        address: '',
        mobile: '',
        email: '',
        pwd: '',
        fromadd: '',
        ext: '',
        teamtype: '0'
      },
      members:[],
      selectedIndex: 0,
      createType: 'school',
      isNext: false,
    }
  },
  methods: {
    handleClose() {
      this.$emit('close');
    },
    handleSelectTab(idx) {
      this.selectedIndex = idx;
      this.isNext = false;
    },
    handleAddMember() {
      let temp = this.members;
      temp.push({
        nick: '',
        role: '',
        department: '',
        mobile: '',
        level: '',
        email: '',
        teacher: ''
        });
      this.members = temp;
      console.log(temp, this.members);
    },
    handleCpSelect() {
      this.isNext = false;
      if(this.createType === 'create') {
        this.placeholder = ''
      } else if(this.createType === 'school'){
        this.placeholder = ''
      } else {
        this.placeholder = ''
      }
      console.log(this.createType);
    },
    handleDeleteMember(idx){
      let temp = this.members;
      temp.splice(idx, 1);
      this.members = temp;
    },
    handleChangeSchoolNick() {
    },
    handleNext() {
      this.isNext = true;
      if(this.selectedIndex == 0) {
        this.schoolParams.ext = JSON.stringify(this.members);
        const schoolParams = this.schoolParams;
        let temp = Object.values(schoolParams);
        for(var i = 0; i < temp.length; i ++) {
          if(temp[i] === '') {
            return false;
            break;
          }
        }
        this.$emit('code', this.schoolParams);
      } else {
        const cpParams = this.cpParams;
        let temp = Object.values(cpParams);
        for(var i = 0; i < temp.length; i ++) {
          if(temp[i] === '') {
            return false;
            break;
          }
        }
        this.$emit('code',this.cpParams);
      }
      
    }
  }
}
</script>

